.item-tile {
    margin: auto;
}


.item-tile-ground {
    margin: auto auto auto auto;
}


.item-tile-air {
    margin: auto auto auto auto;
}

.static-item-tile .item-image-container {
    max-width: 150px;
    max-height: 150px;
}

.static-item-tile .item-image {
    max-width: 150px;
    max-height: 150px;
}

.item-image-container {
    position: relative;
    max-width: 75px;
    max-height: 75px;
}

.item-image {
    max-width: 75px;
    max-height: 75px;
}

.unselected {
    opacity: 0.4;
}

.tooltip {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 100%;
    visibility: hidden;
}

.tooltip .tooltip-content {
    position: absolute;
    z-index: 2;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    min-width: 200px;
    max-width: 400px;
    white-space: nowrap;
    top: 100px;
    left: 10px;
}

.item-tile:hover .tooltip {
    visibility: visible;
}
.item-tile-ground:hover .tooltip {
    visibility: visible;
}
.item-tile-air:hover .tooltip {
    visibility: visible;
}

.petImage {
    max-width: 100%;
    max-height: 100%;
}
